import styled from 'styled-components/macro';
import { Link as ReactRouterLink } from "react-router-dom"

export const Container = styled.div`
    display:flex;
    flex-direction:column;
    min-height:600px;
    background-color:rgba(0,0,0,.75);
    border-radius:5px;
    width:100%;
    margin:auto;
    max-width:300px;
    padding: 60px 68px 40px;
    margin-bottom:100px
`;
export const Error = styled.div`
    background:#e87c03;
    border-radius:5px;
    font-size:14px;
    margin:0 0 16px;
    color:white;
    padding:14px
`;
export const Base = styled.form`
    display:flex;
    flex-direction:column;
    max-width:300px;
    width:100%
`;
export const Title = styled.h1`
    color:#fff;
    font-size:32px;
    font-weight:bold;
    margin-bottom:28px;
`;
export const Text = styled.p`
    color:#737373;
    font-size:16px;
    font-weight:500;
`;
export const TextSmall = styled.p`
    margin-top:10px;
    font-size:16px;
    line-height:normal;
    color:#8c8c8c;
`;
export const Input = styled.input`
    background:#333;
    border-radius:5px;
    border:0;
    color:white;
    height:50px;
    padding:5px 20px;
    margin-bottom:20px;

    &::last-of-type{
        margin-bottom:30px;
    }
`;
export const Submit = styled.button`
    background:#e50914;
    border-radius:4px;
    font-size:16px;
    font-weight:bold;
    margin:24px 0 12px;
    padding:16px;
    border:0;
    color:white;
    cursor:pointer;
    &:disabled{
        opacity:0.5;
        cursor:none;
    }
`;
export const Link = styled(ReactRouterLink)`
    color: white;
    text-decoration:none;

    &:hover{
        text-decoration:underline;
    }
`